import { CompositionReadModelRootRepository } from './composition.read-model-root-repository';
import { CompositionGroupArchive } from './group/composition-group.archive';
import { CompositionId } from '../api/composition.id';
import { CompositionReadModeRoot } from './composition.read-mode-root';
import { CellTemplateWithContext } from './definition/cell-template-with-context';
import { FieldId } from '../../../structure/field/core/domain/field/field.id';
import { SortOrder } from '../domain/column/sort/sort-order';
import { CellTemplateWithAccessor } from './definition/cell-template-with-accessor';
import { GroupCollection } from '../api/group/group.collection';
import { CompositionWarehouse } from '../api/composition.warehouse';
import { Injectable } from '@angular/core';
import { hermesFilter, hermesMap, HermesObservable } from '@generic-ui/hermes';
import { ColumnId } from '../api/column/column.id';
import { ColumnHighlightArchive } from '../domain/highlight/column-highlight.archive';
import { ColumnHighlightManager } from '../domain/highlight/column-highlight.manager';

@Injectable()
export class CompositionDomainWarehouse extends CompositionWarehouse {

	constructor(private readonly compositionRepository: CompositionReadModelRootRepository,
				private readonly compositionGroupArchive: CompositionGroupArchive,
				private readonly columnHighlightArchive: ColumnHighlightArchive) {
		super();
	}

	onWidth(compositionId: CompositionId): HermesObservable<number> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.getWidth())
				   );
	}

	onContainerWidth(compositionId: CompositionId): HermesObservable<number> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.getContainerWidth())
				   );
	}

	onWidthForEachColumn(compositionId: CompositionId): HermesObservable<ReadonlyArray<number>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.getAllColumns().map(c => c.width))
				   );
	}

	onHeaderColumns(compositionId: CompositionId): HermesObservable<ReadonlyArray<CellTemplateWithContext>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.getActiveHeaderColumns())
				   );
	}

	onAllColumns(compositionId: CompositionId): HermesObservable<ReadonlyArray<CellTemplateWithContext>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.getHeaderColumns())
				   );
	}

	onSortOrder(fieldId: FieldId, compositionId: CompositionId): HermesObservable<SortOrder> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.getActiveHeaderColumns()),
					   hermesMap((columns: Array<CellTemplateWithContext>) => {
						   return columns.filter((c) => c.getFieldId().equals(fieldId));
					   }),
					   hermesFilter((columns: Array<CellTemplateWithContext>) => {
						   return columns.length > 0;
					   }),
					   hermesMap(columns => columns[0].getSortStatus())
				   );
	}

	onTemplateColumns(compositionId: CompositionId): HermesObservable<ReadonlyArray<CellTemplateWithAccessor>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.getTemplateColumns())
				   );
	}

	onResizeWidth(compositionId: CompositionId): HermesObservable<boolean> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   hermesMap((composition: CompositionReadModeRoot) => composition.isResizeWidthEnabled())
				   );
	}

	onGroups(compositionId: CompositionId): HermesObservable<GroupCollection> {
		return this.compositionGroupArchive.on(compositionId);
	}

	onHighlightedColumn(columnId: ColumnId, compositionId: CompositionId): HermesObservable<boolean> {
		return this.columnHighlightArchive
				   .on(compositionId)
				   .pipe(
					   hermesMap((manager: ColumnHighlightManager) => manager.isHighlighted(columnId))
				   );
	}

}
