import { SummariesEnabledArchive } from '../domain/enabled/summaries-enabled-archive.service';
import { StructureId } from '../../../core/api/structure.id';
import { SummariesWarehouse } from '../api/summaries.warehouse';
import { Injectable } from '@angular/core';
import { hermesMap, HermesObservable, hermesSwitchMap } from '@generic-ui/hermes';
import { StructureSummariesConfigArchive } from '../domain/config/structure.summaries-config.archive';
import { StructureSummariesPanelConfig } from '../domain/config/structure.summaries-panel.config';

@Injectable()
export class SummariesDomainWarehouse extends SummariesWarehouse {

	constructor(private readonly summariesEnabledArchive: SummariesEnabledArchive,
				private readonly structureSummariesArchive: StructureSummariesConfigArchive) {
		super();
	}

	onEnabled(structureId: StructureId): HermesObservable<boolean> {
		return this.summariesEnabledArchive.on(structureId);
	}

	onBottomEnabled(structureId: StructureId): HermesObservable<boolean> {
		return this.onEnabled(structureId)
				   .pipe(
					   hermesSwitchMap((enabled) => {
						   return this.structureSummariesArchive
									  .on(structureId)
									  .pipe(
										  hermesMap((config: StructureSummariesPanelConfig) => enabled && config.isBottomEnabled())
									  );
					   })
				   );

	}

	onTopEnabled(structureId: StructureId): HermesObservable<boolean> {
		return this.onEnabled(structureId)
				   .pipe(
					   hermesSwitchMap((enabled) => {
						   return this.structureSummariesArchive
									  .on(structureId)
									  .pipe(
										  hermesMap((config: StructureSummariesPanelConfig) => enabled && config.isTopEnabled())
									  );
					   })
				   );
	}

}
