import { Injectable } from '@angular/core';
import { HermesObservable, HermesSubject } from '@generic-ui/hermes';

@Injectable()
export class StructureCellEditCloseAllService {

	private readonly closeAll$ = new HermesSubject<void>();

	closeAll(): void {
		this.closeAll$.next();
	}

	onCloseAll(): HermesObservable<void> {
		return this.closeAll$.toObservable();
	}
}
