import { DomainEvent, DomainEventBus, hermesFilter, hermesMap, HermesObservable, hermesSwitchMap } from '@generic-ui/hermes';
import { StructureReadModelRootId } from '../../../grid/core/api/read/structure.read-model-root-id';
import { StructureId } from '../../../core/api/structure.id';
import { PagingWarehouse } from '../api/paging.warehouse';
import { PrevPageEvent } from '../domain/prev-page/prev-page.event';
import { NextPageEvent } from '../domain/next-page/next-page.event';
import { PageChangedEvent } from '../domain/page/page-changed.event';
import { Paging } from '../api/read/paging';
import { PagesizeChangedEvent } from '../domain/change-pagesize/pagesize-changed.event';
import { PagingEventRepository } from '../api/paging.event-repository';
import { Injectable } from '@angular/core';


@Injectable()
export class PagingDomainEventRepository extends PagingEventRepository {

	constructor(private readonly eventBus: DomainEventBus,
				private readonly pagingWarehouse: PagingWarehouse) {
		super(eventBus);
	}

	onPageChange(structureId: StructureReadModelRootId): HermesObservable<number> {

		return this.eventBus
				   .ofEvent(
					   PrevPageEvent as any,
					   NextPageEvent as any,
					   PageChangedEvent as any
				   )
				   .pipe(
					   hermesFilter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toAggregateId().toString()),
					   hermesSwitchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingWarehouse
									  .oncePaging(structureId.toAggregateId())
									  .pipe(
										  hermesMap((paging: Paging) => paging.getPage())
									  );
					   })
				   );
	}

	onPageSizeChange(structureId: StructureReadModelRootId): HermesObservable<number> {

		return this.eventBus
				   .ofEvent(
					   PagesizeChangedEvent as any
				   )
				   .pipe(
					   hermesFilter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toAggregateId().toString()),
					   hermesSwitchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingWarehouse
									  .oncePaging(structureId.toAggregateId())
									  .pipe(
										  hermesMap((paging: Paging) => paging.getPageSize())
									  );
					   })
				   );
	}

}
