import { Injectable } from '@angular/core';

import { TranslationFacade } from '../api/translation.facade';
import { Translation } from '../api/translation';
import { TranslationChange } from '../api/translation-change';
import { enTranslation } from './lang/en';
import { Dictionary } from './dictionary';
import { HermesObservable, HermesReplaySubject, HermesSubject } from '@generic-ui/hermes';


@Injectable()
export class TranslationDomainFacade extends TranslationFacade {

	private readonly defaultTranslation: Translation = enTranslation;

	private readonly dictionary: Dictionary = new Dictionary();

	private readonly dictionary$: HermesSubject<Translation> = new HermesReplaySubject(1);

	getTranslation(): Translation {
		return this.dictionary.getTranslation();
	}

	onTranslation(): HermesObservable<Translation> {
		return this.dictionary$.toObservable();
	}

	setDefaultTranslation(): void {
		this.changeTranslationAndPropagate(this.defaultTranslation);
	}

	changeTranslation(change: TranslationChange): void {
		this.changeTranslationAndPropagate(change);
	}

	setResolver(resolver: (key: string, value: string) => string): void {
		this.setResolverAndPropagate(resolver);
	}

	private changeTranslationAndPropagate(change: TranslationChange | Translation): void {

		this.dictionary.changeTranslation(change);

		this.dictionary$.next(this.dictionary.getTranslation());
	}

	private setResolverAndPropagate(resolver: (key: string, value: string) => string): void {

		this.dictionary.setResolver(resolver);

		this.dictionary$.next(this.dictionary.getTranslation());
	}
}
