import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { fromRxJsObservable, hermesEmpty, HermesObservable } from '@generic-ui/hermes';
import { Observable, Observer } from 'rxjs';
import { throttleTime } from 'rxjs/operators';

declare const ResizeObserver: any;

export type ObservedSize = {
	bottom: number;
	height: number;
	left: number;
	right: number;
	top: number;
	width: number;
	x: number;
	y: number;
}

@Injectable()
export class ResizeDetector {

	constructor(@Inject(PLATFORM_ID) private platformId: any) {
	}

	on(element: HTMLElement): HermesObservable<ObservedSize> {

		if (isPlatformBrowser(this.platformId)) {

			return fromRxJsObservable(
				(
					new Observable((observer: Observer<any>) => {

						const resizeObserver = new ResizeObserver((entries: any) => {

							if (entries && entries.length > 0) {
								observer.next(entries[0].contentRect);
							}
						});

						resizeObserver.observe(element);

						return () => resizeObserver.disconnect();
					})
				)
					.pipe(
						throttleTime(25)
					)
			);

		}

		return hermesEmpty();
	}

	destroy(element: HTMLElement) {
	}

}
