import { ChangeDetectorRef, ElementRef, EventEmitter } from '@angular/core';
import { GuiComponent } from './gui-component';
import { HermesObservable, HermesSubject, hermesTakeUntil } from '@generic-ui/hermes';

export abstract class SmartComponent extends GuiComponent {

	private viewInDom: boolean = false;

	private readonly hermesUnsubscribe$ = new HermesSubject<void>();

	protected constructor(protected readonly detector: ChangeDetectorRef,
						  elementRef: ElementRef) {
		super(elementRef);
	}

	ngAfterViewInit() {
		this.viewInDom = true;
	}

	ngOnDestroy() {
		this.hermesUnsubscribe();
	}

	reRender(): void {
		if (this.isViewInDom()) {
			this.detector.detectChanges();
		}
	}

	isViewInDom(): boolean {
		return this.viewInDom;
	}

	hermesSubscribe<T>(stream$: HermesObservable<T>, callback: (value: T) => void): void {
		stream$
			.pipe(
				this.hermesTakeUntil()
			)
			.subscribe((streamValues: T) => {
				callback(streamValues);
				this.reRender();
			});
	}

	hermesSubscribeWithoutRender<T>(stream$: HermesObservable<T>, callback: (value: T) => void): void {
		stream$
			.pipe(
				this.hermesTakeUntil()
			)
			.subscribe((streamValues: T) => {
				callback(streamValues);
			});
	}

	// for gate
	subscribeAndEmit<V>(
		stream$: HermesObservable<V>,
		emitter: EventEmitter<V>,
		mapper: (value: V) => any = (value: V) => value
	): void {
		stream$
			.pipe(
				this.hermesTakeUntil()
			)
			.subscribe((value: V) => {
				emitter.emit(mapper(value));
			});
	}

	protected unsubscribe(): void {
		this.hermesUnsubscribe();
	}

	protected hermesUnsubscribe(): void {
		this.hermesUnsubscribe$.next();
		this.hermesUnsubscribe$.complete();
	}

	protected hermesTakeUntil(): any {
		return hermesTakeUntil(this.hermesUnsubscribe$);
	}

}
