import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Inject, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { StructureReadModelRoot } from '../../core/api/read/structure.read-model-root';
import { Paging } from '../../../paging/core/api/read/paging';
import { StructureDefinition } from '../structure-definition';
import { StructureId } from '../../../core/api/structure.id';
import { StructureWarehouse } from '../../core/api/structure.warehouse';
import { PagingWarehouse } from '../../../paging/core/api/paging.warehouse';
import { StructureInfoPanelArchive } from '../../core/api/panel/info/structure.info-panel.archive';
import { FilterWarehouse } from '../../../filter/core/api/filter.warehouse';
import { SearchWarehouse } from '../../../search/core/api/search.warehouse';
import { STRUCTURE_CSS_CLASS_NAME } from '../../core/api/structure-tokens';
import { StructureHeaderTopEnabledArchive } from '../header/structure-header-top-enabled.archive';
import { StructureHeaderBottomEnabledArchive } from '../header/structure-header-bottom-enabled.archive';
import { StructureTitlePanelConfigArchive } from '../panel/banner-panels/title-panel/structure.title-panel.config-archive';
import { StructureFooterPanelConfigArchive } from '../panel/banner-panels/footer-panel/structure.footer-panel.config-archive';

import { StructureInfoPanelConfig } from '../../core/api/panel/info/structure.info-panel.config';
import { StreamCloser } from '../../../../common/cdk/reactive/stream-closer';
import { StructureBannerPanelConfig } from '../panel/banner-panels/structure-banner-panel.config';
import { SummariesWarehouse } from '../../../summaries/core/api/summaries.warehouse';


@Component({
	selector: 'div[gui-structure-blueprint]',
	templateUrl: './structure.blueprint.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureBlueprintComponent extends SmartComponent implements OnInit {

	structure: StructureReadModelRoot;

	searchEnabled: boolean = false;

	topSummariesPanelEnabled: boolean = false;

	bottomSummariesPanelEnabled: boolean = false;

	quickFiltersEnabled: boolean = false;

	infoPanelEnabled: boolean = false;

	readonly contentCssClass: string;

	readonly headerCssClass: string;

	topHeaderEnabled: boolean;

	bottomHeaderEnabled: boolean;

	titlePanelEnabled: boolean;

	footerPanelEnabled: boolean;

	items: Array<any> = [];

	private pagingReadModel: Paging;

	private readonly localStreamCloser = new StreamCloser();

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureDefinition: StructureDefinition,
				private readonly structureId: StructureId,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly structureInfoPanelArchive: StructureInfoPanelArchive,
				private readonly pagingWarehouse: PagingWarehouse,
				private readonly summariesWarehouse: SummariesWarehouse,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly searchWarehouse: SearchWarehouse,
				private readonly structureHeaderTopEnabledArchive: StructureHeaderTopEnabledArchive,
				private readonly structureHeaderBottomEnabledArchive: StructureHeaderBottomEnabledArchive,
				private readonly structureTitlePanelConfigArchive: StructureTitlePanelConfigArchive,
				private readonly structureFooterPanelConfigArchive: StructureFooterPanelConfigArchive,
				@Inject(STRUCTURE_CSS_CLASS_NAME) private className: string) {
		super(changeDetectorRef, elementRef);

		this.headerCssClass = `gui-${this.className}-header`;
		this.contentCssClass = `gui-${this.className}-content`;
	}

	ngOnInit() {

		this.hermesSubscribe(
			this.structureHeaderTopEnabledArchive.on(),
			(topHeaderEnabled: boolean) => {
				this.topHeaderEnabled = topHeaderEnabled;
			}
		);

		this.hermesSubscribe(
			this.structureHeaderBottomEnabledArchive.on(),
			(bottomHeaderEnabled: boolean) => {
				this.bottomHeaderEnabled = bottomHeaderEnabled;
			}
		);

		this.hermesSubscribe(
			this.structureWarehouse.on(this.structureId),
			(structure: StructureReadModelRoot) => {
				this.structure = structure;
				this.items = structure.getEntities();
			}
		);

		this.hermesSubscribe(
			this.pagingWarehouse.onPaging(this.structureId),
			(paging: Paging) => {
				this.pagingReadModel = paging;
			}
		);

		this.hermesSubscribe(
			this.pagingWarehouse.onPaging(this.structureId),
			(paging: Paging) => {
				this.pagingReadModel = paging;
			}
		);

		this.hermesSubscribe(
			this.searchWarehouse.onSearchEnabled(this.structureId),
			(enabled: boolean) => {
				this.searchEnabled = enabled;
			}
		);

		this.hermesSubscribe(
			this.filterWarehouse.onQuickFiltersEnabled(this.structureId),
			(enabled: boolean) => {
				this.quickFiltersEnabled = enabled;
			}
		);

		this.hermesSubscribe(
			this.summariesWarehouse.onBottomEnabled(this.structureId),
			(enabled: boolean) => {
				this.bottomSummariesPanelEnabled = enabled;
			}
		);

		this.hermesSubscribe(
			this.summariesWarehouse.onTopEnabled(this.structureId),
			(enabled: boolean) => {
				this.topSummariesPanelEnabled = enabled;
			}
		);

		this.hermesSubscribe(
			this.structureInfoPanelArchive.on(),
			(infoPanel: StructureInfoPanelConfig) => {
				this.infoPanelEnabled = infoPanel.isEnabled();
			}
		);

		this.hermesSubscribe(
			this.structureTitlePanelConfigArchive.on(),
			(titlePanel: StructureBannerPanelConfig) => {
				this.titlePanelEnabled = titlePanel.enabled;
			}
		);

		this.hermesSubscribe(
			this.structureFooterPanelConfigArchive.on(),
			(footerPanel: StructureBannerPanelConfig) => {
				this.footerPanelEnabled = footerPanel.enabled;
			}
		);
	}

	ngOnDestroy() {
		this.localStreamCloser.unsubscribe();
		super.ngOnDestroy();
	}

	getHeaderTopClasses(): string {
		return this.headerCssClass + ' gui-header-top';
	}

	getHeaderBottomClasses(): string {
		return this.headerCssClass + ' gui-header-bottom';
	}

	isColumnHeaderTopEnabled(): boolean {
		return this.structureDefinition.isHeaderEnabled() && this.topHeaderEnabled;
	}

	isColumnHeaderBottomEnabled(): boolean {
		return this.structureDefinition.isHeaderEnabled() && this.bottomHeaderEnabled;
	}

	isPagingTopEnabled(): boolean {
		return this.structureDefinition.getTopPaging().isEnabled() && this.pagingReadModel && this.pagingReadModel.isPagerTop();
	}

	isPagingBottomEnabled(): boolean {
		return this.structureDefinition.getBottomPaging().isEnabled() && this.pagingReadModel && this.pagingReadModel.isPagerBottom();
	}

	isSourceEmpty(): boolean {
		return this.structure && this.structure.getEntities().length === 0;
	}

	protected getSelectorName(): string {
		return 'gui-structure-blueprint';
	}

}
