import { Directive, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output } from '@angular/core';

import { Gate } from '../../../../../../common/cdk/component/gate';
import { RowColoring } from '../../../../../../schema/core/api/row-coloring/row-coloring';
import { SchemaRowColoring } from '../../../../../../schema/core/api/row-coloring/schema-row-coloring';
import { SchemaCommandInvoker } from '../../../../../../schema/core/api/schema.command-invoker';
import { SchemaEventRepository } from '../../../../../../schema/core/api/schema.event-repository';
import { SchemaReadModelRootId } from '../../../../../../schema/core/api/schema.read-model-root-id';
import { NgChanges } from '../../../../../../common/cdk/component/ng-changes';

@Directive({
	selector: 'gui-structure[rowColoring]'
})
export class StructureRowColoringGate extends Gate implements OnChanges, OnInit, OnDestroy {

	@Input()
	rowColoring: RowColoring;

	@Output()
	rowColoringChanged: EventEmitter<SchemaRowColoring> = new EventEmitter();

	constructor(private readonly schemaId: SchemaReadModelRootId,
				private readonly schemaCommandInvoker: SchemaCommandInvoker,
				private readonly schemaEventRepository: SchemaEventRepository) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureRowColoringGate>) {

		if (this.isDefined('rowColoring', changes)) {
			this.schemaCommandInvoker.setRowColoring(this.rowColoring, this.schemaId);
		}
	}

	ngOnInit() {

		this.subscribeAndEmit(
			this.schemaEventRepository.onRowColoring(this.schemaId),
			this.rowColoringChanged
		);
	}

	ngOnDestroy() {
		super.ngOnDestroy();
	}
}
