import { GuiRowColoring, GuiSelectedRow, GuiTheme } from '../../../core/api/gui.grid.public-api';
import { FormationCommandInvoker } from '../../../../../structure/source/core/api/formation/formation.command-invoker';
import { StructureId } from '../../../../../structure/core/api/structure.id';
import { SourceCommandInvoker } from '../../../../../structure/source/core/api/source.command-invoker';
import { SelectedRow } from '../../../../../structure/source/core/api/formation/selected-row';
import { FormationWarehouse } from '../../../../../structure/source/core/api/formation/formation.warehouse';
import { SearchCommandInvoker } from '../../../../../structure/search/core/api/search.command-invoker';
import { SchemaReadModelRootId } from '../../../../../schema/core/api/schema.read-model-root-id';
import { GuiGridThemeCommandInvoker } from '../theme/gui.grid.theme.command-invoker';
import { StructureCommandInvoker } from '../../../../../structure/core/api/structure.command-invoker';
import { PagingConfig } from '../../../../../structure/paging/core/api/paging-config';
import { PagingCommandInvoker } from '../../../../../structure/paging/core/api/paging.command-invoker';
import { SearchConfig } from '../../../../../structure/search/core/api/search-config';
import { SummariesCommandInvoker } from '../../../../../structure/summaries/core/api/summaries.command-invoker';
import { SortingCommandInvoker } from '../../../../../structure/sorting/core/api/sorting.command-invoker';
import { SortingConfig } from '../../../../../structure/sorting/core/api/sorting-config';


export interface GuiGridApi {

	setSource(source: Array<any>): void

	showLoading(): void;

	hideLoading(): void;

	getSelectedRows(): Array<GuiSelectedRow>;

	selectAll(): void;

	unselectAll(): void;

	deleteSelectedRows(): void;

	deleteRow(row: GuiSelectedRow): void;

	deleteRows(rows: Array<GuiSelectedRow>): void;

	/**
	 * Paging
	 */
	enablePaging(): void;

	disablePaging(): void;

	nextPage(): void;

	prevPage(): void;

	changePageSize(pageSize: number): void;

	setPagingConfig(config: PagingConfig): void;

	/**
	 * Searching
	 */
	setSearchingConfig(config: SearchConfig): void;

	search(phrase: string): void;

	clearSearchPhrase(): void;

	/**
	 * View port
	 */
	scrollToTop(): void;

	scrollToBottom(): void;

	scrollToRowByIndex(index: number): void;

	/**
	 * Visual - Schema
	 */
	setTheme(theme: string | GuiTheme): void;

	setVerticalGrid(enabled: boolean): void;

	setHorizontalGrid(enabled: boolean): void;

	setRowColoring(rowColoring: string | GuiRowColoring): void;

	/**
	 * Virtual scroll
	 */
	enableVirtualScroll(): void;

	disableVirtualScroll(): void;

	/**
	 * Summaries
	 */
	enableSummaries(): void;

	disableSummaries(): void;

	/**
	 * Sorting
	 */
	setSortConfig(config: SortingConfig): void;

}


export class GuiGridApiProvider {

	private readonly structureId: StructureId;

	private readonly schemaId: SchemaReadModelRootId;

	private readonly formationCommandInvoker: FormationCommandInvoker;

	private readonly formationWarehouse: FormationWarehouse;

	private readonly sourceCommandInvoker: SourceCommandInvoker;

	private readonly searchCommandInvoker: SearchCommandInvoker;

	private readonly gridThemeCommandInvoker: GuiGridThemeCommandInvoker;

	private readonly structureCommandInvoker: StructureCommandInvoker;

	private readonly summariesCommandInvoker: SummariesCommandInvoker;

	private readonly sortingCommandInvoker: SortingCommandInvoker;

	private readonly pagingCommandInvoker: PagingCommandInvoker;

	constructor(structureId: StructureId,
				schemaId: SchemaReadModelRootId,
				formationCommandDispatcher: FormationCommandInvoker,
				formationWarehouse: FormationWarehouse,
				sourceCommandInvoker: SourceCommandInvoker,
				searchCommandInvoker: SearchCommandInvoker,
				gridThemeCommandInvoker: GuiGridThemeCommandInvoker,
				structureCommandInvoker: StructureCommandInvoker,
				summariesCommandInvoker: SummariesCommandInvoker,
				sortingCommandInvoker: SortingCommandInvoker,
				pagingCommandInvoker: PagingCommandInvoker) {
		this.structureId = structureId;
		this.schemaId = schemaId;
		this.formationCommandInvoker = formationCommandDispatcher;
		this.formationWarehouse = formationWarehouse;
		this.sourceCommandInvoker = sourceCommandInvoker;
		this.searchCommandInvoker = searchCommandInvoker;
		this.gridThemeCommandInvoker = gridThemeCommandInvoker;
		this.structureCommandInvoker = structureCommandInvoker;
		this.summariesCommandInvoker = summariesCommandInvoker;
		this.sortingCommandInvoker = sortingCommandInvoker;
		this.pagingCommandInvoker = pagingCommandInvoker;
	}

	provide(): GuiGridApi {

		const structureId = this.structureId,
			schemaId = this.schemaId,
			fcd = this.formationCommandInvoker,
			fw = this.formationWarehouse,
			scd = this.sourceCommandInvoker,
			searchCommandInvoker = this.searchCommandInvoker,
			gridThemeCommandInvoker = this.gridThemeCommandInvoker,
			structureCommandInvoker = this.structureCommandInvoker,
			summariesCommandInvoker = this.summariesCommandInvoker,
			sortingCommandInvoker = this.sortingCommandInvoker,
			pagingCommandInvoker = this.pagingCommandInvoker;

		return {

			setSource(source: Array<any>): void {
				scd.setOrigin(source, structureId);
			},

			showLoading(): void {
				scd.setLoading(true, structureId);
			},

			hideLoading(): void {
				scd.setLoading(false, structureId);
			},

			deleteRow(row: GuiSelectedRow): void {
				scd.deleteRow(new SelectedRow(row.source, row.index, row.itemId), structureId);
			},

			deleteRows(rows: Array<GuiSelectedRow>): void {
				const selectedRows = rows.map((row: GuiSelectedRow) => new SelectedRow(row.source, row.index, row.itemId));

				scd.deleteRows(selectedRows, structureId);
			},

			deleteSelectedRows(): void {
				// this.sourceCommandDispatcher.
			},

			getSelectedRows(): Array<GuiSelectedRow> {

				const rows = fw.getSelectedRows(structureId);

				return rows.map((r: SelectedRow) => {
					return { source: r.getSource(), index: r.getIndex(), itemId: r.getItemId() } as GuiSelectedRow;
				});
			},

			selectAll(): void {
				fcd.selectAll(structureId);
			},

			unselectAll(): void {
				fcd.unselectAll(structureId);
			},


			/**
			 * Paging
			 */

			enablePaging(): void {
				pagingCommandInvoker.enable(structureId);
			},

			disablePaging(): void {
				pagingCommandInvoker.disable(structureId);
			},

			nextPage(): void {
				pagingCommandInvoker.nextPage(structureId);
			},

			prevPage(): void {
				pagingCommandInvoker.prevPage(structureId);
			},

			changePageSize(pageSize: number): void {
				pagingCommandInvoker.changePageSize(pageSize, structureId);
			},

			setPagingConfig(config: PagingConfig): void {
				pagingCommandInvoker.setPaging(config, structureId);
			},


			/**
			 * Searching
			 */
			setSearchingConfig(config: SearchConfig): void {
				searchCommandInvoker.setSearchingConfig(config, structureId);
			},

			search(phrase: string): void {
				searchCommandInvoker.search(phrase, structureId);
			},

			clearSearchPhrase(): void {
				searchCommandInvoker.search('', structureId);
			},


			/**
			 * View port
			 */

			scrollToTop(): void {
				structureCommandInvoker.scrollToTop(structureId);
			},

			scrollToBottom(): void {
				structureCommandInvoker.scrollToBottom(structureId);
			},

			scrollToRowByIndex(index: number): void {
				structureCommandInvoker.scrollToIndex(index, structureId);
			},

			/**
			 * Visual - Schema
			 */

			setTheme(theme: string | GuiTheme): void {
				gridThemeCommandInvoker.setTheme(theme, schemaId, structureId);
			},

			setVerticalGrid(enabled: boolean): void {
				gridThemeCommandInvoker.setVerticalGrid(enabled, schemaId);
			},

			setHorizontalGrid(enabled: boolean): void {
				gridThemeCommandInvoker.setHorizontalGrid(enabled, schemaId);
			},

			setRowColoring(rowColoring: string | GuiRowColoring): void {
				gridThemeCommandInvoker.setRowColoring(rowColoring, schemaId);
			},

			/**
			 * Virtual scroll
			 */
			enableVirtualScroll(): void {
				structureCommandInvoker.enableVirtualScroll(structureId);
			},

			disableVirtualScroll(): void {
				structureCommandInvoker.disableVirtualScroll(structureId);
			},

			/**
			 * Summaries
			 */
			enableSummaries(): void {
				summariesCommandInvoker.setSummariesEnabled(true, structureId);
			},

			disableSummaries(): void {
				summariesCommandInvoker.setSummariesEnabled(false, structureId);
			},

			/**
			 * Sorting
			 */
			setSortConfig(config: SortingConfig): void {
				sortingCommandInvoker.setSortingConfig(config, structureId);
			}

		};

	}


}
