import { Directive, Input, OnChanges } from '@angular/core';

import { Gate } from '../../../../common/cdk/component/gate';
import { SummariesConfig } from '../../core/api/summaries-config';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';
import { SummariesCommandInvoker } from '../../core/api/summaries.command-invoker';
import { StructureId } from '../../../core/api/structure.id';


@Directive({
	selector: 'gui-structure[summaries]'
})
export class StructureSummariesGate extends Gate implements OnChanges {

	@Input()
	summaries: SummariesConfig;

	constructor(private readonly structureId: StructureId,
				private readonly summariesCommandInvoker: SummariesCommandInvoker) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureSummariesGate>) {

		if (this.isDefined('summaries', changes)) {
			this.summariesCommandInvoker.setConfig(this.summaries, this.structureId);
		}

	}

}
