import { EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';

import { Gate } from '../../../../common/cdk/component/gate';
import { PagingConfig } from '../../core/api/paging-config';
import { StructureId } from '../../../core/api/structure.id';
import { PagingCommandInvoker } from '../../core/api/paging.command-invoker';
import { PagingEventRepository } from '../../core/api/paging.event-repository';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';
import { CompositionId } from '../../../../composition/core/api/composition.id';


export abstract class PagingGate extends Gate implements OnChanges, OnInit {

	@Input()
	paging: boolean | PagingConfig;

	@Output()
	pageChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	pageSizeChanged: EventEmitter<number> = new EventEmitter<number>();

	protected constructor(protected readonly structureId: StructureId,
						  protected readonly compositionId: CompositionId,
						  protected readonly pagingCommandInvoker: PagingCommandInvoker,
						  protected readonly pagingEventRepository: PagingEventRepository) {
		super();
	}

	ngOnChanges(changes: NgChanges<PagingGate>) {

		if (this.isDefined('paging', changes)) {

			let pagingConfig: PagingConfig;

			if (typeof this.paging === 'boolean') {
				pagingConfig = {
					enabled: this.paging
				};
			} else {
				pagingConfig = this.paging;
			}

			this.pagingCommandInvoker.setPaging(pagingConfig, this.compositionId);
		}
	}

	ngOnInit() {

		this.subscribeAndEmit(
			this.pagingEventRepository.onPageChange(this.structureId.toReadModelRootId()),
			this.pageChanged
		);

		this.subscribeAndEmit(
			this.pagingEventRepository.onPageSizeChange(this.structureId.toReadModelRootId()),
			this.pageSizeChanged
		);
	}

	onPageChange(page: number): void {
		this.pageChanged.emit(page);
	}

}
