import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';

import { CellTemplateWithContext } from '../../../../../../composition/core/domain-read/definition/cell-template-with-context';
import { StructureCommandInvoker } from '../../../../../core/api/structure.command-invoker';
import { StructureColumnConfigService } from '../structure.column-config.service';
import { StructureColumnMenuConfig } from '../config/structure.column-menu-config';
import { SmartComponent } from '../../../../../../common/cdk/component/smart-component';
import { StructureColumnMenuConfigArchive } from '../config/structure.column-menu-config.archive';


@Component({
	selector: 'div[gui-structure-column-config-trigger]',
	templateUrl: './structure.column-config-trigger.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnConfigTriggerComponent extends SmartComponent implements OnInit {

	@ViewChild('headerDialogContainer', { static: false })
	headerDialogContainer: ElementRef;

	@Input()
	column: CellTemplateWithContext;

	config: StructureColumnMenuConfig;

	constructor(private readonly elementRef: ElementRef,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureCommandService: StructureCommandInvoker,
				private readonly structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				private readonly structureColumnConfigService: StructureColumnConfigService) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {
		this.hermesSubscribe(
			this.structureColumnMenuConfigArchive.on(),
			(config: StructureColumnMenuConfig) => {
				this.config = config;
			}
		);
	}

	openConfigDialog(): void {
		this.structureColumnConfigService.open(this.headerDialogContainer, this.column);
	}

	closeConfigDialog(): void {
		this.structureColumnConfigService.close();
	}

	isEnabled(): boolean {
		return this.config && this.config.isEnabled();
	}

	protected getSelectorName(): string {
		return 'gui-structure-column-config-trigger';
	}

}
