import { EventEmitter, OnDestroy } from '@angular/core';

import { NgChanges } from './ng-changes';
import { StreamCloser } from '../reactive/stream-closer';
import { HermesObservable, HermesSubject, hermesTakeUntil } from '@generic-ui/hermes';

export abstract class Gate<T = any> implements OnDestroy {

	private readonly streamCloser = new StreamCloser();

	private readonly hermesUnsubscribe$ = new HermesSubject<void>();

	protected constructor() {
	}

	ngOnDestroy() {
		this.streamCloser.unsubscribe();
		this.hermesUnsubscribe();
	}

	isDefined(propertyName: string, changes: NgChanges<T>): boolean {
		return changes[propertyName] !== undefined && changes[propertyName].currentValue !== undefined;
	}

	subscribeAndEmit<V>(
		stream$: HermesObservable<V>,
		emitter: EventEmitter<V>
	): void {
		stream$
			.pipe(
				this.hermesTakeUntil()
			)
			.subscribe((value: V) => {
				emitter.emit(value);
			});
	}

	protected unsubscribe(): void {
		this.streamCloser.unsubscribe();
	}

	protected hermesUnsubscribe(): void {
		this.hermesUnsubscribe$.next();
		this.hermesUnsubscribe$.complete();
	}

	protected hermesTakeUntil(): any {
		return hermesTakeUntil(this.hermesUnsubscribe$);
	}

	protected takeUntil() {
		return this.streamCloser.takeUntil();
	}

}
