import { HermesObservable, HermesReplaySubject } from '@generic-ui/hermes';

export class ChangedValueEmitter<T> {

	private subject$ = new HermesReplaySubject<T>(1);

	constructor() {
	}

	emit(value: T): void {
		this.subject$.next(value);
	}

	on(): HermesObservable<T> {
		return this.subject$.toObservable();
	}
}
