import { OriginId } from '../../domain/origin/origin-id';
import { OriginItemEntity } from '../../domain/origin/origin-item-entity';

export class SelectedRow {

	private readonly index: number;

	private readonly itemId: OriginId;

	private readonly source: OriginItemEntity;

	constructor(source: OriginItemEntity,
				index: number,
				itemId: OriginId) {
		this.source = source;
		this.index = index;
		this.itemId = itemId;
	}

	getSource(): OriginItemEntity {
		return this.source;
	}

	getIndex(): number {
		return this.index;
	}

	getItemId(): OriginId {
		return this.itemId;
	}

}
