import { Injectable } from '@angular/core';

import { DomainEvent, DomainEventBus, hermesFilter, hermesMap, HermesObservable } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/api/structure.id';
import { EditemItemValues } from './editem-item.values';
import { StructureSourceItemEditedEvent } from '../../domain/origin/edit/structure.source-item-edited.event';


@Injectable()
export class SourceEventService {

	constructor(private readonly domainEventBus: DomainEventBus) {
	}

	onSourceEdited(structureId: StructureId): HermesObservable<EditemItemValues> {

		return this.domainEventBus
				   .ofEvent(
					   StructureSourceItemEditedEvent as any
				   )
				   .pipe(
					   hermesFilter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   hermesMap((event: StructureSourceItemEditedEvent) => {

						   const afterItem = event.getAfterItem().getSourceItem(),
							   beforeItem = event.getBeforeItem().getSourceItem();

						   return new EditemItemValues(afterItem, beforeItem);
					   })
				   );
	}
}
