import { Injectable } from '@angular/core';

import { StructureReadModelRepository } from '../../../grid/core/api/read/structure.read-model-repository';
import { StructureId } from '../../../core/api/structure.id';
import { StructureReadModelRoot } from '../../../grid/core/api/read/structure.read-model-root';
import { ItemEntity } from '../domain/core/item/item.entity';
import { OriginItemEntity } from '../domain/origin/origin-item-entity';
import { hermesDistinctUntilChanged, hermesMap, HermesObservable, hermesTake } from '@generic-ui/hermes';
import { SourceWarehouse } from '../api/source.warehouse';
import { StructurePreparedItemsArchive } from '../domain/prepared/structure.prepared-items.archive';
import { StructureSourceOriginArchive } from '../domain/origin/structure.source-origin.archive';


@Injectable()
export class SourceDomainWarehouse extends SourceWarehouse {

	constructor(private readonly structureRepository: StructureReadModelRepository,
				private readonly structurePreparedItemsRepository: StructurePreparedItemsArchive,
				private readonly structureSourceOriginRepository: StructureSourceOriginArchive) {
		super();
	}

	/**
	 * @deprecated
	 */
	getEntities(structureId: StructureId): ReadonlyArray<ItemEntity> {
		return this.structureRepository.getStructure(structureId).getEntities();
	}

	onEntities(structureId: StructureId): HermesObservable<ReadonlyArray<ItemEntity>> {

		return this.structureRepository
				   .on(structureId)
				   .pipe(
					   hermesMap((structure: StructureReadModelRoot) => structure.getEntities()),
					   hermesDistinctUntilChanged((ents1: ReadonlyArray<ItemEntity>, ents2: ReadonlyArray<ItemEntity>) => {
						   if (ents1.length !== ents2.length) {
							   return false;
						   }

						   let flag = true;

						   ents1.forEach((ent, index) => {
							   if (!ent.equals(ents2[index])) {
								   flag = false;
								   return;
							   }
						   });

						   return flag;
					   })
				   );
	}

	onEntitiesSize(structureId: StructureId): HermesObservable<number> {

		return this.onEntities(structureId)
				   .pipe(
					   hermesMap((entities: ReadonlyArray<ItemEntity>) => {
						   return entities.length;
					   })
				   );
	}

	onceEntities(structureId: StructureId): HermesObservable<ReadonlyArray<ItemEntity>> {

		return this.onEntities(structureId)
				   .pipe(
					   hermesTake(1)
				   );
	}

	onOriginSize(structureId: StructureId): HermesObservable<number> {
		return this.structureSourceOriginRepository
				   .on(structureId)
				   .pipe(
					   hermesMap(origin => origin.length)
				   );
	}

	onLoading(structureId: StructureId): HermesObservable<boolean> {
		return this.structureRepository
				   .on(structureId)
				   .pipe(
					   hermesMap((structure: StructureReadModelRoot) => structure.getSource().isLoading())
				   );
	}

	onPreparedEntities(structureId: StructureId): HermesObservable<ReadonlyArray<OriginItemEntity>> {
		return this.structurePreparedItemsRepository.on(structureId);
	}

	/**
	 * @deprecated
	 */
	getPreparedEntities(structureId: StructureId): ReadonlyArray<OriginItemEntity> {
		return this.structurePreparedItemsRepository.getPreparedItems(structureId);
	}

}
