import { GuiColumnCellEditing } from '../../../../../../gui.grid.index';
import { ColumnCellEditingConfig } from '../../../../../../composition/core/api/column/column-cell-editing.config';

export class GuiGridColumnCellEditingConverter {

	convert(config: boolean | GuiColumnCellEditing): ColumnCellEditingConfig {

		if (typeof config === 'boolean') {
			return {
				enabled: config
			};
		}

		return config as ColumnCellEditingConfig;
	}

}
