import { Directive, Input, OnChanges } from '@angular/core';

import { Gate } from '../../../../common/cdk/component/gate';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';
import { StructureCommandInvoker } from '../../../core/api/structure.command-invoker';
import { QuickFiltersConfig } from '../../core/api/quick-filters.config';
import { StructureId } from '../../../core/api/structure.id';

@Directive({
	selector: 'gui-structure[quickFilters]'
})
export class StructureQuickFiltersGate extends Gate implements OnChanges {

	@Input()
	quickFilters: boolean | QuickFiltersConfig;

	constructor(private readonly structureId: StructureId,
				private readonly structureCommandInvoker: StructureCommandInvoker) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureQuickFiltersGate>) {

		if (this.isDefined('quickFilters', changes)) {

			let quickFilters: QuickFiltersConfig;

			if (typeof this.quickFilters === 'boolean') {
				quickFilters = {
					enabled: this.quickFilters
				};
			} else {
				quickFilters = this.quickFilters;
			}

			this.structureCommandInvoker.setQuickFiltersConfig(quickFilters, this.structureId);
		}

	}

}
