import { SmartComponent } from '../../../../../common/cdk/component/smart-component';
import { ChangeDetectorRef, ElementRef, OnInit } from '@angular/core';
import { StructureBannerPanelConfig } from './structure-banner-panel.config';
import { HermesObservable } from '@generic-ui/hermes';

export abstract class StructureBannerPanel extends SmartComponent implements OnInit {

	bannerPanel: string | (() => string);

	protected constructor(changeDetectorRef: ChangeDetectorRef,
						  elementRef: ElementRef) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.hermesSubscribe(
			this.onBannerPanelConfig(),
			(config: StructureBannerPanelConfig) => {

				this.bannerPanel = config.template;

				if (typeof this.bannerPanel === 'function') {
					this.bannerPanel = this.bannerPanel();
				}
			}
		);
	}

	abstract onBannerPanelConfig(): HermesObservable<StructureBannerPanelConfig>

	abstract getSelectorName(): string

	abstract getPanelTitle(): string
}
