import { Gate } from '../../../../common/cdk/component/gate';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';
import { StructureCommandInvoker } from '../../../core/api/structure.command-invoker';
import { Directive, Input, OnChanges } from '@angular/core';
import { StructureId } from '../../../core/api/structure.id';

@Directive({
	selector: 'gui-structure[virtualScroll]'
})
export class VerticalFormationGate extends Gate implements OnChanges {

	@Input()
	virtualScroll: boolean;

	constructor(private readonly structureId: StructureId,
				private readonly structureCommandInvoker: StructureCommandInvoker) {
		super();
	}

	ngOnChanges(changes: NgChanges<VerticalFormationGate>): void {

		if (this.isDefined('virtualScroll', changes)) {
			if (this.virtualScroll) {
				this.structureCommandInvoker.enableVirtualScroll(this.structureId);
			} else {
				this.structureCommandInvoker.disableVirtualScroll(this.structureId);
			}
		}
	}

}
