import { Injectable } from '@angular/core';

import { DomainEventBus, EventRepository, hermesFilter, hermesMap, HermesObservable, HermesSubject, hermesSwitchMap, hermesTake } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/api/structure.id';
import { SourceWarehouse } from '../source.warehouse';
import { SelectedRow } from './selected-row';
import { SelectedRowChangedEvent } from '../../domain/formation/selected-row-changed.event';
import { OriginItemEntity } from '../../domain/origin/origin-item-entity';
import { StructureReadModelRootId } from '../../../../grid/core/api/read/structure.read-model-root-id';


@Injectable()
export class FormationEventRepository extends EventRepository<StructureReadModelRootId, StructureId> {

	private readonly map = new Map();

	private readonly subject$ = new HermesSubject();

	constructor(domainEventBus: DomainEventBus,
				private readonly sourceWarehouse: SourceWarehouse) {
		super(domainEventBus);

		domainEventBus
			.ofEvent(
				SelectedRowChangedEvent as any
			)
			.pipe(
				hermesSwitchMap((event: SelectedRowChangedEvent) => {

					const selectedRows = event.getSelectedRows();
					const structureId = event.getAggregateId();

					return this.sourceWarehouse
							   .onPreparedEntities(structureId)
							   .pipe(
								   hermesTake(1),
								   hermesMap((items: Array<OriginItemEntity>) => {

									   const selectedItems = [],
										   length = items.length,
										   itemsMap = new Map<string, OriginItemEntity>();

									   for (let i = 0; i < length; i += 1) {
										   itemsMap.set(items[i].getId().toString(), items[i]);
									   }

									   for (let j = 0; j < selectedRows.length; j += 1) {
										   const item: OriginItemEntity = itemsMap.get(selectedRows[j]);
										   selectedItems.push(new SelectedRow(item.getSourceItem(), j, item.getId()));
									   }

									   return {
										   id: structureId,
										   items: selectedItems
									   };
								   })
							   );
				})
			)
			.subscribe((e) => {
				this.map.set(e.id.toString(), e.items);
				this.subject$.next(this.map);
			});
	}

	onItemSelected(structureId: StructureId): HermesObservable<ReadonlyArray<SelectedRow>> {
		return this.subject$
				   .toObservable()
				   .pipe(
					   hermesFilter((map: any) => {
						   return map.has(structureId.toString());
					   }),
					   hermesMap((map: any) => {
						   return map.get(structureId.toString());
					   })
				   );
	}

}
