import { AggregateArchive, AggregateId, hermesFilter, HermesObservable, hermesTake } from '@generic-ui/hermes';
import { Injectable } from '@angular/core';

@Injectable()
export class StructureInitialValuesReadyArchive extends AggregateArchive<boolean> {

	static readonly default = false;

	constructor() {
		super(StructureInitialValuesReadyArchive.default);
	}

	on(aggregateId: AggregateId): HermesObservable<boolean> {
		return super.on(aggregateId)
					.pipe(
						hermesFilter(v => v)
					);
	}

	once(aggregateId: AggregateId): HermesObservable<boolean> {
		return this.on(aggregateId)
				   .pipe(
					   hermesTake(1)
				   );
	}

}
