import { Gate } from '../../../../common/cdk/component/gate';
import { SchemaCommandInvoker } from '../../../core/api/schema.command-invoker';
import { SchemaEventRepository } from '../../../core/api/schema.event-repository';
import { SchemaReadModelRootId } from '../../../core/api/schema.read-model-root-id';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';
import { Directive, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';


@Directive({
	selector: 'gui-structure[verticalGrid][horizontalGrid]'
})
export class ThemeGridGate extends Gate implements OnChanges, OnInit {

	@Input()
	verticalGrid: boolean;

	@Input()
	horizontalGrid: boolean;

	@Output()
	horizontalGridChanged: EventEmitter<boolean> = new EventEmitter();

	@Output()
	verticalGridChanged: EventEmitter<boolean> = new EventEmitter();

	constructor(private readonly schemaId: SchemaReadModelRootId,
				private readonly schemaCommandInvoker: SchemaCommandInvoker,
				private readonly schemaEventRepository: SchemaEventRepository) {
		super();
	}

	ngOnChanges(changes: NgChanges<ThemeGridGate>): void {

		if (this.isDefined('verticalGrid', changes)) {
			this.schemaCommandInvoker.setVerticalGrid(this.verticalGrid, this.schemaId);
		}

		if (this.isDefined('horizontalGrid', changes)) {
			this.schemaCommandInvoker.setHorizontalGrid(this.horizontalGrid, this.schemaId);
		}
	}

	ngOnInit() {

		this.subscribeAndEmit(
			this.schemaEventRepository.onHorizontalGridChanged(this.schemaId),
			this.horizontalGridChanged
		);

		this.subscribeAndEmit(
			this.schemaEventRepository.onVerticalGridChanged(this.schemaId),
			this.verticalGridChanged
		);
	}
}
