import { DomainEventBus, hermesMap, HermesObservable } from '@generic-ui/hermes';
import { SchemaReadModelRootId } from '../api/schema.read-model-root-id';
import { SchemaTheme } from '../api/theme/schema-theme';
import { SchemaThemeSetEvent } from './theme/schema-theme-set.event';
import { SchemaHorizontalGridSetEvent } from './grid/horizontal/schema-horizontal-grid-set.event';
import { SchemaVerticalGridSetEvent } from './grid/vertical/schema-vertical-grid-set.event';
import { SchemaRowColoring } from '../api/row-coloring/schema-row-coloring';
import { RowColoringSetEvent } from './row-coloring/row-coloring-set.event';
import { SchemaEventRepository } from '../api/schema.event-repository';
import { Injectable } from '@angular/core';

@Injectable()
export class SchemaDomainEventRepository extends SchemaEventRepository {

	constructor(domainEventBus: DomainEventBus) {
		super(domainEventBus);
	}

	onThemeChanged(schemaId: SchemaReadModelRootId): HermesObservable<SchemaTheme> {

		return this.onEvent(schemaId, SchemaThemeSetEvent)
				   .pipe(
					   hermesMap((event: SchemaThemeSetEvent) => {
						   return event.getTheme();
					   })
				   );
	}

	onHorizontalGridChanged(schemaId: SchemaReadModelRootId): HermesObservable<boolean> {

		return this.onEvent(schemaId, SchemaHorizontalGridSetEvent)
				   .pipe(
					   hermesMap((event: SchemaHorizontalGridSetEvent) => event.getHorizontalGrid())
				   );
	}

	onVerticalGridChanged(schemaId: SchemaReadModelRootId): HermesObservable<boolean> {

		return this.onEvent(schemaId, SchemaVerticalGridSetEvent)
				   .pipe(
					   hermesMap((event: SchemaVerticalGridSetEvent) => event.getVerticalGrid())
				   );
	}

	onRowColoring(schemaId: SchemaReadModelRootId): HermesObservable<SchemaRowColoring> {

		return this.onEvent(schemaId, RowColoringSetEvent)
				   .pipe(
					   hermesMap((event: RowColoringSetEvent) => event.getRowColoring())
				   );
	}

}
