import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SearchingEnabledArchive } from './searching-enabled.archive';

import { ConfigSearchingSetEvent } from '../config/config-searching-set.event';
import { StructureId } from '../../../../core/api/structure.id';


@Injectable()
export class ConfigSearchingSetEventHandler implements DomainEventHandler<StructureId, ConfigSearchingSetEvent> {

	constructor(private readonly structureSearchingRepository: SearchingEnabledArchive) {
	}

	forEvent(): DomainEventType<ConfigSearchingSetEvent> {
		return ConfigSearchingSetEvent;
	}

	handle(event: ConfigSearchingSetEvent): void {

		if (event.ofMessageType('ConfigSearchingSetEvent')) {
			this.structureSearchingRepository.next(event.getAggregateId(), event.isEnabled());
		}

	}

}
