import { Component, ContentChild, TemplateRef, Input } from '@angular/core';
import {
	FieldAccessor,
	GuiCellView, GuiColumn,
	GuiColumnAlign, GuiColumnCellEditing,
	GuiColumnSorting,
	GuiColumnSummaries,
	GuiDataType,
	ViewTemplateFunction
} from '../../../core/api/gui.grid.public-api';

@Component({
	selector: 'gui-grid-column',
	template: ''
})
export class GuiGridColumnComponent implements GuiColumn {

	@ContentChild(TemplateRef, { static: true })
	templateRef: TemplateRef<any>;

	@Input()
	field?: string | FieldAccessor;

	@Input()
	type?: string | GuiDataType;

	@Input()
	view?: string | GuiCellView | ViewTemplateFunction;

	@Input()
	header?: string | ViewTemplateFunction;

	@Input()
	width?: string | number;

	@Input()
	enabled?: boolean;

	@Input()
	align?: string | GuiColumnAlign;

	@Input()
	summaries?: GuiColumnSummaries;

	@Input()
	sorting?: boolean | GuiColumnSorting;

	@Input()
	cellEditing?: boolean | GuiColumnCellEditing;

	@Input()
	formatter?: (item: any, index: number) => any;

	@Input()
	matcher?: (item: any) => any;

}
