export class StructureSummariesPanelConfig {

	static readonly defaultTop = false;

	static readonly defaultBottom = true;

	private top: boolean = StructureSummariesPanelConfig.defaultTop;

	private bottom: boolean = StructureSummariesPanelConfig.defaultBottom;

	setTop(top: boolean): void {
		this.top = top;
	}

	setBottom(bottom: boolean): void {
		this.bottom = bottom;
	}

	isTopEnabled(): boolean {
		return this.top;
	}

	isBottomEnabled(): boolean {
		return this.bottom;
	}

}
