import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { FilterEnabledArchive } from './filter-enabled.archive';
import { ConfigFilterSetEvent } from '../../domain/config/config-filter-set.event';
import { StructureId } from '../../../../core/api/structure.id';


@Injectable()
export class ConfigFilterSetEventHandler implements DomainEventHandler<StructureId, ConfigFilterSetEvent> {

	constructor(private readonly structureFilterRepository: FilterEnabledArchive) {
	}

	forEvent(): DomainEventType<ConfigFilterSetEvent> {
		return ConfigFilterSetEvent;
	}

	handle(event: ConfigFilterSetEvent): void {

		if (event.ofMessageType('ConfigFilterSetEvent')) {
			this.structureFilterRepository.next(event.getAggregateId(), event.getEnabled());
		}

	}

}
