import { ChangeDetectionStrategy, Component, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

import { structureRowDetailViewItem, structureRowDetailViewTemplate } from './structure.row-detail-view.tokens';
import { SelectedRow } from '../../../../source/core/api/formation/selected-row';
import { DynamicallyCreatedComponent } from '../../../../../common/cdk/component/dynamically-created-component';
import { OriginItemEntity } from '../../../../source/core/domain/origin/origin-item-entity';


@Component({
	selector: 'div[gui-row-detail]',
	templateUrl: './structure.row-detail-view.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureRowDetailViewComponent extends DynamicallyCreatedComponent implements OnInit {

	selectedRowValue: OriginItemEntity;

	safeHTML: SafeHtml;

	constructor(@Inject(structureRowDetailViewItem) private readonly item: SelectedRow,
				@Inject(structureRowDetailViewTemplate) private readonly template: any,
				private readonly sanitizer: DomSanitizer) {
		super();

		this.safeHTML = this.sanitizer.bypassSecurityTrustHtml(this.template(this.item.getSource(), this.item.getIndex()));
	}

	ngOnInit(): void {
		this.selectedRowValue = this.item.getSource();
	}

}
