import { HermesSubject, hermesTakeUntil } from '@generic-ui/hermes';


export class StreamCloser<T> {

	private readonly unsubscribe$ = new HermesSubject<void>();

	constructor() {
	}

	takeUntil() {
		return hermesTakeUntil(this.unsubscribe$);
	}

	unsubscribe(): void {
		if ((this.unsubscribe$ as any).isCompleted) {
			return;
		}

		this.unsubscribe$.next();
		this.unsubscribe$.complete();
	}

}
