import { DomainEventBus, hermesFilter, hermesMap, HermesObservable } from '@generic-ui/hermes';
import { CompositionReadModelRootId } from '../api/composition.read-model-root-id';
import { CompositionColumnsSetEvent } from './column/set-columns/composition.columns-set.event';
import { CompositionContainerWidthSetEvent } from './width/set-container-width/composition-container-width-set.event';
import { CompositionEventRepository } from '../api/composition.event-repository';
import { Injectable } from '@angular/core';

@Injectable()
export class CompositionDomainEventRepository extends CompositionEventRepository {

	constructor(domainEventBus: DomainEventBus) {
		super(domainEventBus);
	}

	onColumnsChanged(compositionId: CompositionReadModelRootId): HermesObservable<CompositionColumnsSetEvent> {

		return this.onEvent(compositionId, CompositionColumnsSetEvent);
	}

	onContainerWidthChanged(compositionId: CompositionReadModelRootId): HermesObservable<number> {

		return this.onEvent(compositionId, CompositionContainerWidthSetEvent)
				   .pipe(
					   hermesMap((event: CompositionContainerWidthSetEvent) => {
						   return event.getPayload();
					   }),
					   hermesFilter((width: number) => !!width)
				   );
	}

}
