import { Injectable } from '@angular/core';

import { HermesObservable, hermesTake } from '@generic-ui/hermes';

import { StructureId } from '../../../core/api/structure.id';
import { PagingRepository } from '../api/read/paging.repository';
import { Paging } from '../api/read/paging';
import { PagingWarehouse } from '../api/paging.warehouse';


@Injectable()
export class PagingDomainWarehouse extends PagingWarehouse {

	constructor(private readonly pagingRepository: PagingRepository) {
		super();
	}

	onPaging(structureId: StructureId): HermesObservable<Paging> {
		return this.pagingRepository.on(structureId);
	}

	oncePaging(structureId: StructureId): HermesObservable<Paging> {
		return this.pagingRepository
				   .on(structureId)
				   .pipe(
					   hermesTake(1)
				   );
	}

}
