import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { Placement } from '@generic-ui/fabric';

import { SortOrder } from '../../../../../../../composition/core/domain/column/sort/sort-order';
import { CellTemplateWithContext } from '../../../../../../../composition/core/domain-read/definition/cell-template-with-context';
import { CompositionWarehouse } from '../../../../../../../composition/core/api/composition.warehouse';
import { SmartComponent } from '../../../../../../../common/cdk/component/smart-component';
import { SortingCommandInvoker } from '../../../../../../sorting/core/api/sorting.command-invoker';
import { CompositionId } from '../../../../../../../composition/core/api/composition.id';
import { StructureId } from '../../../../../../core/api/structure.id';


@Component({
	selector: 'div[gui-structure-column-config-sort]',
	templateUrl: './structure.column-config-sort.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnConfigSortComponent extends SmartComponent implements OnInit {

	@Input()
	column: CellTemplateWithContext;

	@Input()
	dropdownTextTranslation: string;

	placement: Placement = Placement.Right;

	status = SortOrder;

	sortStatus: SortOrder;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly compositionId: CompositionId,
				private readonly structureId: StructureId,
				private readonly sortingCommandDispatcher: SortingCommandInvoker,
				private readonly compositionReadModelService: CompositionWarehouse) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {
		this.sortStatus = this.column.getSortStatus();

		this.hermesSubscribe(
			this.compositionReadModelService.onSortOrder(this.column.getFieldId(), this.compositionId),
			(sortStatus: SortOrder) => {
				this.sortStatus = sortStatus;
			}
		);
	}

	isAscSort(): boolean {
		return this.sortStatus === SortOrder.ASC;
	}

	isDescSort(): boolean {
		return this.sortStatus === SortOrder.DESC;
	}

	isNoneSort(): boolean {
		return this.sortStatus === SortOrder.NONE;
	}

	setSortOrder(sort: SortOrder): void {
		event.stopPropagation();
		this.sortingCommandDispatcher.setSortOrder(this.column.getFieldId(), sort, this.compositionId, this.structureId);
	}

	protected getSelectorName(): string {
		return 'gui-structure-column-config-sort';
	}
}
