import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { StructureId } from '../../../../../core/api/structure.id';
import { FormationCommandInvoker } from '../../../../../source/core/api/formation/formation.command-invoker';
import { SmartComponent } from '../../../../../../common/cdk/component/smart-component';
import { FormationWarehouse } from '../../../../../source/core/api/formation/formation.warehouse';
import { RowSelectedReadModel } from '../../../../../source/core/api/formation/row-selected.read-model';
import { RowSelectionModeRepository } from '../../../../../source/core/api/formation/mode/row-selection-mode.repository';
import { RowSelectionMode } from '../../../../../source/core/api/row-selection';

@Component({
	selector: 'div[gui-select-all]',
	templateUrl: './select-all.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class SelectAllComponent extends SmartComponent implements OnInit {

	selectAllChecked: boolean;

	selectAllIndeterminate: boolean;

	modeMulti: boolean = false;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly rowSelectionModeRepository: RowSelectionModeRepository,
				private readonly formationCommandDispatcher: FormationCommandInvoker,
				private readonly formationWarehouse: FormationWarehouse) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.hermesSubscribe(
			this.formationWarehouse.onMode(this.structureId),
			(mode: RowSelectionMode) => {
				this.modeMulti = mode === RowSelectionMode.MULTIPLE;
			}
		);

		this.hermesSubscribe(
			this.formationWarehouse.onRowSelectedReadModel(this.structureId),
			(rowSelectedReadModel: RowSelectedReadModel) => {
				this.selectAllChecked = rowSelectedReadModel.isAllSelected();
				this.selectAllIndeterminate = rowSelectedReadModel.isIndeterminate();
			}
		);
	}

	toggleSelectAll(): void {

		if (this.selectAllIndeterminate) {
			this.formationCommandDispatcher.unselectAll(this.structureId);
		} else {

			if (this.selectAllChecked) {
				this.formationCommandDispatcher.unselectAll(this.structureId);
			} else {
				this.formationCommandDispatcher.selectAll(this.structureId);
			}
		}
	}

	protected getSelectorName(): string {
		return 'gui-select-all';
	}

}
